import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import ProductTable from "components/Evenets/EventsList";
import { Link } from "react-router-dom";
import { GetAllEvents } from "services/Api";
import TableLoading from "components/preloader/TableLoading";

const Products = (serviceData) => {
  const { t } = useTranslation();
  const { toggleDrawer, lang, handleSubmitForAll } = useContext(SidebarContext);
  const [pagenumber, setpagenumber] = useState(1);
  const [loading, setLoading] = useState(true);
  const [AllEventsList, setAllEventsList] = useState([]);
  const [wholeCount, setwholeCount] = useState({});
  useEffect(() => {
    getAllEvaentsList(pagenumber);
  }, []);

  const getAllEvaentsList = async (pagenumber, search = "") => {
    const EventsList = await GetAllEvents(pagenumber, search);
    console.log(EventsList.data.data?.events, "GetAllEvents");
    setLoading(false);
    setAllEventsList(EventsList?.data?.data);
    setwholeCount(EventsList?.data?.data?.pagination);
  };
  const handleChangePage = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getAllEvaentsList(e);
  };

  const handleSearch = (search) => {
    getAllEvaentsList(pagenumber, search);
  };

  return (
    <>
      <PageTitle>{t("Events")}</PageTitle>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Link to="/add-events">
                  {" "}
                  <Button
                    // onClick={toggleDrawer}
                    className="w-full rounded-md h-12"
                  >
                    <span className="mr-2">
                      <FiPlus />
                    </span>
                    {t("Add Events")}
                  </Button>{" "}
                </Link>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell className="text-left">{"s.no"}</TableCell>
                <TableCell>{t("Name")}</TableCell>
                <TableCell>{t("Events Details")}</TableCell>
                <TableCell>{t("Property")}</TableCell>
                <TableCell>{t("Activities")}</TableCell>
                <TableCell>{t("checkin Date & time")}</TableCell>
                <TableCell>{t("CheckOut Date & time")}</TableCell>
                <TableCell>{t("Buffets")}</TableCell>
                <TableCell>{t("Address")}</TableCell>
                <TableCell>{t("Seats")}</TableCell>
                <TableCell>{t("Price")}</TableCell>
                <TableCell>{t("Organiser details")}</TableCell>
                {/* <TableCell>{t("Blocked Date")}</TableCell> */}
                <TableCell
                  className=""
                  style={{ width: "250px", display: "block" }}
                >
                  {t("Image")}
                </TableCell>
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>
            <ProductTable
              lang={lang}
              products={AllEventsList?.events}
              getAllEvaentsList={getAllEvaentsList}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={AllEventsList?.pagination?.total}
              resultsPerPage={AllEventsList?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Events" />
      )}
    </>
  );
};

export default Products;
