import React, { useContext, useState } from "react";
import { useLocation } from "react-router-dom";
import { Modal, ModalBody, ModalFooter, Button } from "@windmill/react-ui";
import { FiTrash2 } from "react-icons/fi";
import ProductServices from "services/ProductServices";
import { SidebarContext } from "context/SidebarContext";
import { notifySuccess, notifyError } from "utils/toast";
import useToggleDrawer from "hooks/useToggleDrawer";
import { DeleteEvents } from "services/Api";
import spinnerLoadingImage from "../../assets/img/spinner.gif";

const MainModal = ({ id, getAllEvaentsList }) => {
  const { isModalOpen, closeModal } = useContext(SidebarContext);
  const { setServiceId } = useToggleDrawer();
  const [isSubmitting, setisSubmitting] = useState(false);
  const handleDelete = async () => {
    setisSubmitting(true);
    console.log("dleete from modal", "ww", id);
    try {
      setisSubmitting(true);
      const response = await DeleteEvents(id);
      console.log(response, "ww");
      closeModal();
      getAllEvaentsList(1);
      setisSubmitting(false);
      notifySuccess(response.data.message);
      setServiceId();
    } catch (err) {
      notifySuccess(err.message);
      console.log(err);
      setisSubmitting(false);
    }
  };

  return (
    <>
      <Modal isOpen={isModalOpen} onClose={closeModal}>
        <ModalBody className="text-center custom-modal px-8 pt-6 pb-4">
          <span className="flex justify-center text-3xl mb-6 text-red-500">
            <FiTrash2 />
          </span>
          <h2 className="text-xl font-medium mb-1">
            Are You Sure! Want to Delete{" "}
            <span className="text-red-500">{`Event`}</span>
          </h2>
          <p>
            Do you really want to delete these records? You can't view this in
            your list anymore if you delete!
          </p>
        </ModalBody>
        <ModalFooter className="justify-center">
          <Button
            className="w-full sm:w-auto hover:bg-white hover:border-gray-50"
            layout="outline"
            onClick={closeModal}
          >
            No, Keep It
          </Button>
          {isSubmitting ? (
            <Button disabled={true} type="button" className="h-12">
              <img
                src={spinnerLoadingImage}
                alt="Loading"
                width={20}
                height={10}
              />{" "}
              <span className="font-serif ml-2 font-light">Processing</span>
            </Button>
          ) : (
            <Button onClick={handleDelete} className="w-full sm:w-auto">
              Yes, Delete It
            </Button>
          )}
        </ModalFooter>
      </Modal>
    </>
  );
};

export default React.memo(MainModal);
