import {
  Avatar,
  Badge,
  TableBody,
  TableCell,
  TableRow,
} from "@windmill/react-ui";
import DeleteModal from "components/Evenets/DeleteEvents";
import ShowHideButton from "components/table/ShowHideButton";
import defalutimage from "../../assets/img/forgot-password-office-dark.jpeg";
import Tooltip from "components/tooltip/Tooltip";
import useToggleDrawer from "hooks/useToggleDrawer";
import { t } from "i18next";
import { FiEdit, FiTrash2, FiZoomIn } from "react-icons/fi";
import { useHistory, useLocation } from "react-router-dom";
import { SidebarContext } from "context/SidebarContext";
import { useContext, useState } from "react";
import moment from "moment";

//internal import

const ProductTable = ({
  products,
  isCheck,
  setIsCheck,
  getAllEvaentsList,
  wholeCount,
  lang,
}) => {
  const { title, handleUpdate } = useToggleDrawer();
  const [serviceId, setserviceId] = useState("");
  const { toggleModal, toggleDrawer1, toggleDrawer, setIsDrawerOpen1 } =
    useContext(SidebarContext);
  const history = useHistory();
  const handleClick = (e) => {
    const { id, checked } = e.target;
    console.log("id", id, checked);

    setIsCheck([...isCheck, id]);
    if (!checked) {
      setIsCheck(isCheck.filter((item) => item !== id));
    }
  };

  const handleUpdate1 = (id) => {
    console.log("clicked");
    history.replace(`/edit-events/${id}`);
  };

  const handleModalOpen = (data) => {
    toggleModal();
    console.log("clicked---", data);
    setserviceId(data);
  };
  const convertToAmPm = (time) => {
    const [hours = 0, minutes = 0] = time?.split(":");
    const parsedHours = parseInt(hours, 10);
    const period = parsedHours >= 12 ? "PM" : "AM";
    const formattedHours = parsedHours % 12 === 0 ? 12 : parsedHours % 12;
    return `${formattedHours}:${minutes} ${period}`;
  };
  return (
    <>
      <DeleteModal
        getAllEvaentsList={getAllEvaentsList}
        id={serviceId}
        title={title}
      />
      <TableBody>
        {products?.map((properties, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="text-sm">
                {(wholeCount?.currentPage - 1) * wholeCount?.pageSize + i + 1}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">{properties?.name}</span>
            </TableCell>
            <TableCell className="textcorrect">
              <span className="text-sm">
                {properties?.event_details?.length > 100
                  ? (properties?.event_details).substring(0, 100 - 3) + "..."
                  : properties?.event_details}
              </span>
              {/* <span className="text-sm">{properties?.event_details}</span> */}
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.Property?.name}</span>
            </TableCell>
            <TableCell className="">
              <ul>
                {properties?.Event_activities?.map((bro) => (
                  <li className="text-sm">{bro?.Activity?.name + ","}</li>
                ))}
              </ul>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {moment(properties?.start_date).format("YYYY-MM-DD")}
              </span>
              <br />
              <span className="text-sm">
                {properties?.start_time
                  ? convertToAmPm(properties?.start_time)
                  : null}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {moment(properties?.end_date).format("YYYY-MM-DD")}
              </span>
              <br />
              <span className="text-sm">
                {properties?.end_time
                  ? convertToAmPm(properties?.end_time)
                  : null}
              </span>
            </TableCell>
            <TableCell className="">
              <ul>
                {properties?.Event_buffets?.map((bro) => (
                  <li className="text-sm">{bro?.Buffet?.name}</li>
                ))}
              </ul>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.venue_details}</span>
            </TableCell>
            <TableCell>
              {properties?.slots_availability > 0 ? (
                <Badge type="success">
                  {t(properties?.slots_availability)}
                </Badge>
              ) : (
                <Badge type="danger">{t(properties?.slots_availability)}</Badge>
              )}
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {"Adult: " + properties?.price_with_gst}
              </span>
              <br />
              <span className="text-sm">
                {"Child: " + properties?.price_child_with_gst}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {"Organised By: " + properties?.organised_by}
              </span>
              <br />
              <span className="text-sm">
                {"Organiser Name: " + properties?.organiser_name}
              </span>
              <br />
              <span className="text-sm">
                {"Organiser Email: " + properties?.organiser_email}
              </span>
              <br />
              <span className="text-sm">
                {"Organiser Phone: " + properties?.organiser_contact_number}
              </span>
            </TableCell>
            {/* <TableCell className="">
              {properties?.Event_block_dates?.map((bro) => (
                <span className="text-sm">
                  {moment(bro?.date).format("MMMM Do YYYY")}
                  <br />
                </span>
              ))}
            </TableCell> */}
            <TableCell>
              {properties?.Event_images?.map((img) => (
                <img
                  className="object-cover imgCustom"
                  src={img.image}
                  alt="Office"
                />
              ))}
            </TableCell>
            {/* <TableCell className="text-center">
              <ShowHideButton id={properties._id} status={properties.status} />
            </TableCell> */}
            <TableCell>
              <button
                onClick={() => handleUpdate1(properties.id)}
                className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
              >
                <Tooltip
                  id="edit"
                  Icon={FiEdit}
                  title={t("Edit1")}
                  bgColor="#10B981"
                />
              </button>
              <button
                onClick={() =>
                  handleModalOpen(properties?.id, "title", "product")
                }
                className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
              >
                <Tooltip
                  id="delete"
                  Icon={FiTrash2}
                  title={t("Delete")}
                  bgColor="#EF4444"
                />
              </button>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default ProductTable;
